body{
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: whitesmoke;
    height: 100%;
}

html,
body,
#root,

.container {
    height: 100%;
  }

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 50px;
    align-items: flex-start;
    margin-left: 10px;
    box-shadow: black;
}

.movies .movie {
    display: flex;
    width: 45%;
    background-color: white;
    font-weight: 300;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 70px;
    padding: 30px;
}

.movie img {
    max-width:  120px;
    margin-top: -50px;
    margin-right: 30px;
}

.movie h3 .movie h5{
    font-weight: 300;
}
